  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@keyframes ice-cream-shadow-spawn {
  0% {
    filter: drop-shadow(0 0 #fcfcfc);
  }

  100% {
    filter: drop-shadow(0 0 40px #fcfcfc);
  }
}

@keyframes ice-cream-shadow-flash {
  0% {
    filter: drop-shadow(0 0 40px #fcfcfc);
  }

  100% {
    filter: drop-shadow(0 0 10px #fcfcfc);
  }
}

@keyframes ice-cream-rotate {
  0% {
    opacity: 0;
    transform: scale(0%);
  }

  100% {
    opacity: 1;
    transform: scale(100%)rotate(2turn);
  }
}

@keyframes eye-close {
  0% {
    transform: scaleY(100%);
  }

  48% {
    transform: scaleY(100%);
  }

  50% {
    transform: scaleY(0%);
  }

  52% {
    transform: scaleY(100%);
  }

  100% {
    transform: scaleY(100%);
  }
}

@keyframes plate-rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(1turn);
  }
}

@keyframes gallery {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  33% {
    opacity: 1;
  }

  43% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

